
import { defineComponent, reactive, ref } from "vue";
import { HullCheck, Airclair } from "@/store/resi2";
import {
  CatamaranMoment,
  CatamaranPassenger,
  CatamaranText,
} from "@/store/catamaran";

export default defineComponent({
  name: "HomeView",
  setup() {
    const Hull = new HullCheck(Airclair);
    const data = reactive({
      w: 0.27,
      y: 6.6,
      s: 11.2,
      l: 1277,
      f0: 1.2,
    });
    const dataC = reactive({
      b: 0,
      Bp: 0,
      F2: 0,
    });
    const result = reactive({ WGM: 0, Mt: 0, d_: 0, M_c: 0, n_c: 0 });
    const check = () => {
      const r = Hull.calc(data.w, data.y, data.s, data.l, data.f0);
      result.WGM = r.WGM;
      result.Mt = r.Mt;
      result.d_ = r.d_;
    };
    const check_c = () => {
      result.n_c = CatamaranPassenger(Hull.state.L, Hull.state.B, dataC.F2);
      result.M_c = CatamaranMoment(
        Hull.state.N,
        Hull.state.Zp,
        Hull.result.d_,
        Hull.state.B,
        dataC.Bp,
        data.f0,
        Hull.state.L,
        dataC.b
      );
    };
    const showDetail = ref(false);
    return {
      Hull,
      data,
      check,
      result,
      showDetail,
      CatamaranText,
      dataC,
      check_c,
    };
  },
});
